@charset 'UTF-8';
*,
*:after,
*:before {
  -mox-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
body {
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-size: 16px;
  font-smoothing: antialiased;
  text-size-adjust: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-weight: 700;
}
ol,
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
p {
  font-weight: 500;
}
p b,
p strong {
  font-weight: 700;
}
img {
  max-width: 100%;
}
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700");
.x-footer {
  background-color: #6f6f6f;
  height: 56px;
  clear: both;
}
.x-footer::after {
  content: '';
  display: block;
  clear: both;
}
.x-footer::before {
  content: '';
  display: block;
  clear: both;
}
.x-footer .x-logo {
  margin-top: 18px;
  display: inline-block;
  width: 50%;
  float: left;
}
.x-footer .x-copy {
  display: inline-block;
  width: 50%;
  float: left;
  margin-top: 18px;
  text-align: right;
}
.x-footer .x-copy .x-copyRight {
  color: #b7b7b7;
  font-size: 14px;
}
.x-footer .x-copy .x-copyRight .x-mad {
  text-decoration: underline;
}
.x-header {
  background-color: #6f6f6f;
  clear: both;
}
@media only screen and (min-width: 1024px) {
  .x-header::before {
    content: '';
    display: block;
    clear: both;
  }
  .x-header::after {
    content: '';
    display: block;
    clear: both;
  }
}
.x-header .x-logo {
  padding: 17.5px 0;
}
@media only screen and (min-width: 1024px) {
  .x-header .x-logo {
    display: inline-block;
    width: 40%;
    float: left;
    font-size: 18px;
  }
}
.x-header .container {
  position: relative;
}
.x-header .x-nav {
  display: block;
  width: 100%;
  transition: all 300ms ease;
  background-color: #dbdbdb;
  text-align: center;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 99;
  right: -100%;
}
@media only screen and (min-width: 1024px) {
  .x-header .x-nav {
    display: inline-block;
    width: 60%;
    float: left;
    position: initial;
    height: auto;
    background-color: transparent;
  }
}
.x-header .x-nav .x-navList {
  margin-top: 40px;
}
@media only screen and (min-width: 1024px) {
  .x-header .x-nav .x-navList {
    margin-top: 0;
  }
}
.x-header .x-nav .x-navList .x-listItem {
  width: 100%;
  padding: 10px 0;
}
@media only screen and (min-width: 1024px) {
  .x-header .x-nav .x-navList .x-listItem {
    display: inline-block;
    width: auto;
    padding: 17.5px 10px;
  }
}
.x-header .x-nav .x-navList .x-listItem a {
  color: #000;
  text-transform: uppercase;
}
@media only screen and (min-width: 1024px) {
  .x-header .x-nav .x-navList .x-listItem a {
    color: #fff;
    font-weight: 400;
    font-size: 18px;
    transition: all 200ms ease;
  }
  .x-header .x-nav .x-navList .x-listItem a:hover {
    color: #01ff96;
  }
}
.x-header .x-hamburguer {
  display: block;
  width: 27px;
  height: 5px;
  transition: all 300ms ease;
  background-color: #fff;
  position: absolute;
  right: 5px;
  top: 15px;
}
@media only screen and (min-width: 1024px) {
  .x-header .x-hamburguer {
    display: none;
  }
}
.x-header .x-hamburguer::after {
  content: '';
  display: block;
  width: 27px;
  height: 5px;
  background-color: #fff;
  position: relative;
  right: 0px;
  top: 3px;
}
.x-header .x-hamburguer::before {
  content: '';
  display: block;
  width: 27px;
  height: 5px;
  background-color: #fff;
  position: relative;
  right: 0px;
  top: 16px;
}
.x-header .x-navActive {
  right: 0;
}
.x-header .x-hamburguerActive {
  position: fixed;
  right: 15px;
  top: 24px;
  transform: rotate(40deg);
  z-index: 999;
  background-color: #f00;
}
.x-header .x-hamburguerActive::before {
  display: none;
}
.x-header .x-hamburguerActive::after {
  transform: rotate(100deg);
  top: 0;
  background-color: #f00;
}
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track::before,
.slick-track::after {
  content: "";
  display: table;
}
.slick-track::after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.container {
  width: 1200px;
  max-width: 95%;
  margin-left: auto;
  margin-right: auto;
}
body {
  color: #6f6f6f;
}
.x-green {
  color: #01ff96;
}
.x-logo {
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
}
.x-secondSection {
  text-align: center;
}
@media only screen and (min-width: 1024px) {
  .x-secondSection {
    text-align: left;
    background-color: #dbdbdb;
    margin-bottom: 40px;
    margin-top: 20px;
  }
}
@media only screen and (min-width: 1024px) {
  .x-secondSection .x-first {
