}
.x-secondSection .x-firstBanners .x-list__firstBanners li img {
  height: 100%;
}
@media only screen and (min-width: 1024px) {
  .x-secondSection .x-firstBanners .x-list__firstBanners li img {
    width: 100%;
  }
}
@media only screen and (min-width: 768px) {
  .x-secondSection .x-secondBanners {
    width: 448px;
    margin: 0 auto;
  }
}
@media only screen and (min-width: 1024px) {
  .x-secondSection .x-secondBanners {
    width: 50%;
