  }
}
@media only screen and (min-width: 1024px) {
  .x-secondSection .x-secondBanners .x-secondDescription {
    order: 1;
  }
}
.x-secondSection .x-secondBanners .x-list__secondBanners {
  position: relative;
}
.x-secondSection .x-secondBanners .x-list__secondBanners li img {
  height: 100%;
}
@media only screen and (min-width: 1024px) {
  .x-secondSection .x-secondBanners .x-list__secondBanners li img {
    width: 100%;
  }
}
.x-firstSection {
  display: block;
  width: 100%;
  padding: 30px 0 20px 0;
  text-align: center;
}
.x-firstSection .x-title {
  font-size: 36px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 5px;
  color: #404040;
}
.x-firstSection .x-description {
